'use client';

import { useState } from 'react';
import '../app/aiagent.css';

interface AIAgent {
  id: string;
  name: string;
  voiceType: string;
  languageModel: string;
  lastChat: string;
}

export default function AIAgentList() {
  const [agents, setAgents] = useState<AIAgent[]>([]);
  const [showModal, setShowModal] = useState(false);
  const [newAgentName, setNewAgentName] = useState('');

  const handleAddAgent = () => {
    if (!newAgentName.trim()) {
      alert('请输入智能体名称');
      return;
    }

    const newAgent: AIAgent = {
      id: Date.now().toString(),
      name: newAgentName.trim(),
      voiceType: '湾湾小何',
      languageModel: 'DeepSeek V3 0324',
      lastChat: '7 小时前'
    };

    setAgents([...agents, newAgent]);
    setNewAgentName('');
    setShowModal(false);
  };

  const handleDeleteAgent = (id: string) => {
    setAgents(agents.filter(agent => agent.id !== id));
  };

  return (
    <div>
      <main>
        <div className="container">
          <div className="breadcrumb">
            <span>控制台</span>
            <span>/</span>
            <span>智能体</span>
          </div>

          <button
            onClick={() => setShowModal(true)}
            className="new-ai-btn"
          >
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <line x1="12" y1="5" x2="12" y2="19"></line>
              <line x1="5" y1="12" x2="19" y2="12"></line>
            </svg>
            新建智能体
          </button>
        </div>

        <div className="ai-list">
          {agents.map(agent => (
            <div key={agent.id} className="ai-item">
              <div className="ai-item-header">
                <h3>{agent.name}</h3>
                <button
                  onClick={() => handleDeleteAgent(agent.id)}
                  className="close-btn"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                    <line x1="18" y1="6" x2="6" y2="18"></line>
                    <line x1="6" y1="6" x2="18" y2="18"></line>
                  </svg>
                </button>
              </div>
              <div className="ai-item-info">
                <div className="info-row">
                  <span className="label">角色音色：</span>
                  <span className="value">{agent.voiceType}</span>
                </div>
                <div className="info-row">
                  <span className="label">语言模型：</span>
                  <span className="value">{agent.languageModel}</span>
                </div>
                <div className="info-row">
                  <span className="label">最近对话：</span>
                  <span className="value">{agent.lastChat}</span>
                </div>
              </div>
              <div className="ai-item-tabs">
                <button className="tab-btn">配置角色</button>
                <button className="tab-btn">声纹识别</button>
                <button className="tab-btn">历史对话</button>
                <button className="tab-btn">管理设备 (1)</button>
              </div>
            </div>
          ))}
        </div>
      </main>

      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <div className="modal-header">
              <h2>新建智能体</h2>
              <button
                onClick={() => setShowModal(false)}
                className="close-btn"
              >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                  <line x1="18" y1="6" x2="6" y2="18"></line>
                  <line x1="6" y1="6" x2="18" y2="18"></line>
                </svg>
              </button>
            </div>
            <div className="modal-body">
              <div className="form-group">
                <label>
                  <span className="required">*</span>
                  智能体名称：
                </label>
                <input
                  type="text"
                  value={newAgentName}
                  onChange={(e) => setNewAgentName(e.target.value)}
                  placeholder="请输入智能体名称"
                />
              </div>
            </div>
            <div className="modal-footer">
              <button onClick={() => setShowModal(false)}>取消</button>
              <button onClick={handleAddAgent}>确定</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}